.gallery { // div .galleryid-3783.gallery-columns-5.gallery-size-medium
	margin-top: 2rem;
	margin-bottom: 2rem;
}


.gallery-columns-1 {
}

@media($sm_) {
	.gallery-columns-2 {
		display: flex;
		flex-wrap: wrap;
		.gallery-item {
			flex-basis: 48%;
			margin: 1%;
		}
	}
}

.gallery-columns-3,
.gallery-columns-4,
.gallery-columns-5,
.gallery-columns-6 {
	display: flex;
	flex-wrap: wrap;
	.gallery-item {
		flex-basis: 48%;
		margin: 1%;
	}
}
@media($_sm){
	.gallery-columns-5,
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 30%;
			margin: 1%;
		}
	}
}
@media($sm_md){
	.gallery-columns-4 {
		.gallery-item {
			flex-basis: 30%;
			margin: 1%;
		}
	}
	.gallery-columns-5 {
		.gallery-item {
			flex-basis: 23%;
			margin: 1%;
		}
	}
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 18%;
			margin: 1%;
		}
	}
}
@media($md_){
	.gallery-columns-3 {
		.gallery-item {
			flex-basis: 30%;
			margin: 1%;
		}
	}
	.gallery-columns-4 {
		.gallery-item {
			flex-basis: 23%;
			margin: 1%;
		}
	}
	.gallery-columns-5 {
		.gallery-item {
			flex-basis: 18%;
			margin: 1%;
		}
	}
	.gallery-columns-6 {
		.gallery-item {
			flex-basis: 14%;
			margin: 1%;
		}
	}
}

  .gallery-item {//figure()
      // margin-bottom: 1.5rem;
  }


.gallery-columns-2,
.gallery-columns-3,
.gallery-columns-4,
.gallery-columns-5,
.gallery-columns-6 {
	display: flex;
	flex-wrap: wrap;
	.gallery-item { // need for visual editor
		width: 100%;
	}
	// 3, 4 ,52
	@media($_sm){
		grid-template-columns: 1fr 1fr;
		gap: .5rem .25rem;
		font-size: $font-small;
	}
}

    .gallery-icon {//div
    	position: relative;
        text-align: center;
        a {
          img {
          	width: 100%;
          }
          &:after {
			@include my-fa-icon-base;
			font-family: "Font Awesome 5 Free";
			font-weight: 900; // regular  400
			content: "\f065";

          	position: absolute;
          	right: 8px;
          	bottom: 5px;
			color: white;
			opacity: .7;
          }
        }
    }
    .gallery-caption {//figcaption.wp-caption-textTheme Check 
        text-align: center;
        line-height: 1.1;
    }

