/**
 * Object (image, movie, iframe etc.)
 */

//  -> _wp-gallery.scss

img {
	max-width: 100%;
	height: auto;
	vertical-align: text-bottom;
	// pointer-events: none;
}
a:hover img { // 
	opacity: .8;
	// filter: brightness(102%);// filter: blur(  ); contrast(  or  );
}
a:active img { // 
	position: relative;
	left: 1px;
	top: 1px;
}

// iframediv.embed_container
.embed-container {
	position: relative;
	padding-bottom: 56.25%;// 100%
	padding-top: 30px;
	height: 0;
	overflow: hidden;
	max-width: 100%;
	height: auto;
	iframe, object, embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.rounded--images,
.rounded-images--3,
.rounded-images--5,
.rounded-images--7,
.rounded-images--10,
.circle--images,
.box-shadow-images {
	img {
		// Box ShadowBlur -> _base.scss
		//  img 
	}
}


@media($_sm){
	.img--mobile-small,
	.img--mobile-x-small {
		margin-right: auto;
		margin-left: auto;
	}
	.img--mobile-small {
		width: 75%;
	}
	.img--mobile-x-small {
		width: 50%;
	}
}
