/**
 * 
 * Breadcrumb NavXT
 * 
 * Bootstrap 
 */
.breadcrumbs {
	margin-top: .5rem;
	margin-bottom: 2rem;
	font-size: $font-small;
	a {
		display: inline-block;
		color: $color-gray-600;
		&:hover {
			color: $color-link;
		}
	}
}



/**
 * 
 *   header.php 
 */
.top-header { // container
	padding-top: 2rem;
	padding-bottom: 2rem;
	background-color: $color-gray-300;
}
	.top-header__title { // p
		margin-bottom: 0;
		text-align: center;
		font-size: 1.5rem;
	}


/**
 * ""
 *   TODO: stiky 
 */
.header-important-information { // aside
	width: 100%;
	margin-top: 1px;
	margin-bottom: 1px;
	font-size: $font-small;
	text-align: center;
	ul {
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		list-style-type: none;
	}
		li {
			background-color: $color-secondary;
			&:not(:first-child){
				border-top: 1px solid #FFF;
			}
		}
		a {
			text-decoration: underline;
		}
}
