/**
 * ContactForm 7
 */
.wpcf7-list-item { // span
	display: block !important;
}
.wpcf7c-btn-confirm {
	@extend .btn--secondary;
}

/**
 * Breadcrumb NavXT ()
 */
.breadcrumbs {
	span[property="name"] { // 
		display: inline-block;
		max-width: 10rem;
		vertical-align: middle;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
		a[property="item"] {
			//
		}
		.home {} // 
			span[property="name"] {
				//
			}
}

/**
 * Wordpress Related Posts ()
 */
.wp_rp_wrap {
	margin-top: 3rem;
}
	.wp_rp_content {
	}
	.related_post_title { // h3
		margin-bottom: 1.5rem;
		border-bottom: 1px solid $color-primary-light;
		font-size: 1.2rem;
	}
	.related_post { // ul
		padding-left: 0;
		list-style-type: none;
	}
		.related_post li {}
		.wp_rp_thumbnail { // a
			display: block;
			text-align: center;
		}
			.wp_rp_thumbnail img {
				max-width: 100%;
			}
		.wp_rp_title { // a
			display: block;
			margin-top: .5rem;
			font-size: $font-small;
			line-height: 1.2;
			word-break : break-all;
		}



