#primary-menu-toggle-button {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  padding: 0;
  font-size: 1.5rem;
  color: #868E96;
  background-color: #FFF;
  border: none;
}

#primary-menu-toggle-button:before {
  content: '';
}

.primary-menu-toggle-button--open:before {
  content: '' !important;
}

/**
		 * 
		 *    ID primary-menu.php  wp_nav_menu() 
		 *   @link  https://wpdocs.osdn.jp//wp_nav_menu
		 */
#primary-menu ul {
  padding-left: 0;
  list-style-type: none;
}

#primary-menu ul > li a {
  padding: .5rem;
}

#primary-menu .menu-item a {
  display: block;
  font-size: 0.875rem;
  text-decoration: none;
  color: #333;
}

#primary-menu .sub-menu {
  z-index: 999;
}

#primary-menu a:before, #primary-menu a:after {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

@media (max-width: 768px) {
  #primary-menu {
    display: none;
  }
  #primary-menu.primary-menu--open {
    display: block;
    animation: mobile-menu .4s linear 0s;
  }
  @keyframes mobile-menu {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  #primary-menu > ul {
    margin-top: 0;
    margin-bottom: 0;
  }
  #primary-menu a:after {
    float: right;
    content: "\f105";
    color: #e3dacb;
  }
  #primary-menu .menu-item a {
    background-color: white;
    border-bottom: #E9ECEF 1px solid;
  }
  #primary-menu .menu-item a:hover {
    background-color: #F8F9FA;
  }
  #primary-menu .menu-item a:before {
    margin-right: .5rem;
    content: "\f138";
    color: #AD9468;
  }
  #primary-menu .sub-menu a {
    padding-left: 2rem;
  }
  #primary-menu .sub-menu a:before {
    margin-right: .5rem;
    content: "\f0da";
    color: #e3dacb;
  }
}

/**
		 *  (flex)
		 */
@media (min-width: 769px) {
  #primary-menu-toggle-button {
    display: none;
  }
  #primary-menu > ul {
    display: inline-flex;
  }
  #primary-menu > ul > .menu-item-home {
    display: none !important;
  }
  #primary-menu .menu-item-has-children {
    position: relative;
  }
  #primary-menu .menu-item-has-children > a:after {
    margin-left: .25rem;
    content: "\f0d7";
    color: #e3dacb;
  }
  #primary-menu .sub-menu {
    display: none;
    position: absolute;
    border: #E9ECEF 1px solid;
    box-shadow: 1px 1px 0 1px rgba(0, 0, 0, 0.2);
  }
  #primary-menu .sub-menu li {
    width: 12rem;
  }
  #primary-menu .sub-menu a {
    background-color: white;
    border-bottom: #E9ECEF 1px dotted;
  }
  #primary-menu .sub-menu a:hover {
    background-color: #F8F9FA;
  }
  #primary-menu .sub-menu a:before {
    margin-right: .5rem;
    content: "\f0da";
    color: #e3dacb;
  }
  #primary-menu .menu-item-has-children:hover .sub-menu {
    display: block;
    animation: show-sub-menu .4s linear 0s;
  }
  @keyframes show-sub-menu {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}

