/**
 * Speech Bubble
 *
 * TODO: 
 */
.bubble {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.bubble__speaker {
  width: 20%;
  margin-right: 1rem;
  text-align: center;
  font-size: .8rem;
}

.bubble__speaker img {
  width: 100%;
  border-radius: 100%;
}

.bubble__speech {
  width: 75%;
  position: relative;
  padding: 1rem;
  background: #FFF;
  border-radius: .5rem;
  border: #E7E7E7 solid 3px;
}

.bubble__speech:before,
.bubble__speech:after {
  content: '';
  position: absolute;
  border-style: solid;
  border-width: 1rem 1rem 1rem 0;
  display: block;
  width: 0;
  margin-top: -1rem;
  top: 50%;
}

.bubble__speech:before {
  border-color: transparent #E7E7E7;
  z-index: 0;
  left: -17px;
}

.bubble__speech:after {
  border-color: transparent #FFF;
  z-index: 1;
  left: -13px;
  /* (before ) */
}

.bubble__speech *:first-child {
  margin-top: 0;
}

.bubble__speech *:last-child {
  margin-bottom: 0;
}

.bubble--reverse .bubble__speaker {
  order: 2;
}

.bubble--reverse .bubble__speech {
  order: 1;
  margin-left: 0;
  margin-right: 2rem;
}

.bubble--reverse .bubble__speech:before,
.bubble--reverse .bubble__speech:after {
  border-width: 1rem 0 1rem 1rem;
}

.bubble--reverse .bubble__speech:before {
  left: initial;
  right: -17px;
}

.bubble--reverse .bubble__speech:after {
  left: initial;
  right: -13px;
}

@media (max-width: 768px) {
  .bubble {
    align-items: flex-start;
  }
  .bubble:before,
  .bubble:after {
    top: 3rem;
  }
  .bubble__speaker {
    margin-top: 1rem;
    margin-right: .5rem;
  }
  .bubble__speaker img {
    width: 60px;
  }
  .bubble__speech:before,
  .bubble__speech:after {
    top: 2rem;
  }
  .bubble--reverse .bubble__speech {
    margin-right: 1.5rem;
  }
}

/**
 * accordion
 *
 * @link https://saruwakakun.com/html-css/reference/accordion
 */
.accordion {
  margin-top: 1rem;
}

.accordion label {
  display: block;
  margin-top: 1px;
  padding: .5rem 1rem;
  color: #FFF;
  font-weight: bold;
  background: #AD9468;
  cursor: pointer;
  transition: all .5s;
}

.accordion label:after {
  float: right;
  content: '';
}

.accordion label:hover {
  background: #e3dacb;
}

.accordion input {
  display: none;
}

.accordion__content {
  height: 0;
  padding: 0 1rem;
  overflow: hidden;
  transition: .5s;
}

.accordion__open_flag:checked + .accordion__content {
  height: auto;
  padding-top: 5px;
  padding-bottom: 5px;
}

