/**
 * Grid Layout
 */
@mixin inner-bottom-margin($size) {
	display: block;
	content: '';
	margin-bottom: $size;
}

.grid,
.grid--narrow,
.grid--wide {
	margin-top: 1rem;
	margin-bottom: 1rem;
	@media($sm_) {
		display: flex;
		flex-wrap: wrap;
	}
}

