// 2/3
.grid--mobile-2col,
.grid--mobile-3col {
	display: flex;
	flex-wrap: wrap;
	font-size: smaller;
}

@media($_sm) {
	.grid--mobile-2col {
		.grid__item {
			flex-basis: 48%;
			margin: 0 1%;
			&:after {
				@include inner-bottom-margin(2%);
			}
		}
		&.grid--gap-none {
			.grid__item {
				flex-basis: 50%;
				margin: 0;
			}
		}
		&.grid--gap-narrow {
			.grid__item {
				flex-basis: 49%;
				margin: 0 .5%;
			}
			&:after {
				@include inner-bottom-margin(1%);
			}
		}
		&.grid--gap-wide {
			.grid__item {
				flex-basis: 46%;
				margin: 0 2%;
				&:after {
					@include inner-bottom-margin(4%);
				}
			}
		}
	}
}

@media($_md){
	.grid--mobile-3col {
		.grid__item {
			flex-basis: 31.3% !important;
			margin: 0 .5%;
			&.grid--gap-none {
				.grid__item {
					flex-basis: 33.3%;
					margin: 0;
				}
			}
			&.grid--gap-narrow {
				.grid__item {
					flex-basis: 32.83%;
					margin: 0 .25%;
					&:after {
						@include inner-bottom-margin(.5%);
					}
				}
			}
			&.grid--gap-wide {
				.grid__item {
					flex-basis: 31.3%;
					margin: 0 1%;
					&:after {
						@include inner-bottom-margin(2%);
					}
				}
			}
		}
	}
}

