/**
 * 
 * ============================================================
 * nav[role="navigation"] 
 *  ID primary-menu.php  wp_nav_menu() 
 */

/*
 * Stracture
 *
 * header#masthead.site-header
 * 	.container
 * 		.site-branding
 * 			.site-title
 * 			.catch-phrase
 * 		button#primary-menu-toggle-button
 * 		nav#primary-menu
 * 			ul
 * 				li.menu-item.menu-item-has-children.
 * 					a
 * 						ul.sub-menu
 * 							li.menu-item.menu-item-has-children.
 */



// @media($md_){
// 	.home #masthead {
// 		position: absolute;
// 		width: 100%;
// 		top: 0;
// 	}
// }
.site-header { // &#masthead
	z-index: 998;
	.site-header-inner { // &.container--no-margin, flex wrapper
		@media($_lg) {
			width: 100%;
		}
		@media($lg_) {
			display: flex;
        	align-items: center;
		}
	}
}

		/**
		 * 
		 */
		.site-branding {
			@media($_lg){
				text-align: center;
			}
			@media($_md) {
				height: 40px;
				background-color: #FFF;
			}
			@media($md_lg) {
				margin-right: auto;
				margin-left: auto;
			}
			@media($md_){
				max-width: 16rem;
			}

		}
			.site-title,
			.catch-phrase {

			}
			.site-title {//h1p 
				margin-top: .5rem;
				margin-bottom: .5rem;
				font-size: $font-x-large; // 
				font-weight: bold; // 
				line-height: 1;
				@media($_md){
					margin-top: .5rem;
					margin-bottom: .25rem;
				}
				.custom-logo-link { // a
					// custom logo common settings -> _wp-essential.scss
				}
					.custom-logo { // img
						@media($lg_) {
							width: 100% !important;
						}
					}
			}
			.catch-phrase { // ()
				margin-top: 0;
				margin-bottom: .5rem;
				font-size: $font-x-small;
				@media($_md) {
					display: none;
				}
			}

		/**
		 *  / 
		 */
		#primary-menu-toggle-button { // button
			position: absolute;
			top: 0;
			left: 0;
			width: 40px;
			height: 40px;
			padding: 0;
			font-size: $font-x-large;
			color: $color-gray-600;
			background-color: #FFF;
			border: none;
			// border: 1px solid $color-gray-400;
			&:before {
				content: '';
			}
		}
		.primary-menu-toggle-button--open:before {
			content: '' !important;
		}
		.admin-bar #primary-menu-toggle-button {
			// top: 2.875rem;
		}

		/**
		 * 
		 *    ID primary-menu.php  wp_nav_menu() 
		 *   @link  https://wpdocs.osdn.jp//wp_nav_menu
		 */

		// 
		#primary-menu { // nav
			ul {
				padding-left: 0;
				list-style-type: none;
				& > li {
					a {
						padding: .5rem;
					}
				}
			}

				.menu-item { // li 
					a {
						display: block;
						font-size: $font-small;
						text-decoration: none;
						color: $color-font;
					}
				}
				.menu-item-has-children {} //  li

					.sub-menu { // ul 2
						z-index: 999;
						li {}
							a {}
					}
					a {
						&:before,
						&:after {
							@include my-fa-icon-base;
							font-family: "Font Awesome 5 Free";
							font-weight: 900; // regular  400
						}
					}


				// li 
				.current-menu-item {
					a {}
				}

				// 
				.current-menu-item         {} // li 

				.current-menu-parent       {} // li 
				.current-OBJECT-parent     {} // li 
				.current-TYPE-parent       {} // li 

				.current-menu-ancestor     {} // li 
				.current-OBJECT-ancestor   {} // li 
				.current-TYPE-ancestor     {} // li 

				.menu-item-home            {} // li  

				.page_item                 {} // li 
				.page_item_has_children    {} // li 
				.page-item-ID              {} // li $IDID

				.current_page_item         {} // li 
				.current_page_parent       {} // li 
				.current_page_ancestor     {} // li 

				.menu-item-object-category {} // li 
				.menu-item-object-tag      {} // li 
				.menu-item-object-page     {} // li 
				.menu-item-object-CUSTOM   {} // li 

				.menu-item-type-post_type  {} // li 
				.menu-item-type-taxonomy   {} // li 
		}


		// 

		@media($_md) {
			#primary-menu {
				display: none;
				&.primary-menu--open {
					display: block;
					animation: mobile-menu .4s linear 0s;
					@keyframes mobile-menu {
						from { opacity: 0; }
						  to { opacity: 1; }
					}
				}
				& > ul {
					margin-top: 0;
					margin-bottom: 0;
				}
				& > ul > .menu-item > a { // 1
					// padding-top: 1rem;
					// padding-bottom: 1rem;
				}
				a:after {
					float: right;
					content: "\f105";
					color: $color-primary-light;
				}
				.menu-item { // li
					a {
						background-color: white;
						border-bottom: $color-gray-200 1px solid;
						&:hover {
							background-color: $color-gray-100;
						}
						&:before {
							margin-right: .5rem;
							content: "\f138";
							color: $color-primary;
						}
					}
				}
					.sub-menu {
						li {}
							a {
								padding-left: 2rem;;
								&:before {
									margin-right: .5rem;
									content: "\f0da";
									color: $color-primary-light;
								}
							}
					}
			}
		}


		/**
		 *  (flex)
		 */
		@media($md_) {
			#primary-menu-toggle-button {
				display: none;
			}

			#primary-menu {
				> ul {
					display:inline-flex;
					> .menu-item-home {
						display: none !important;
					}
				}
					.menu-item {} // li
					.menu-item-has-children { //  li
						position: relative;
						> a:after {
							margin-left: .25rem;
							content: "\f0d7";
							color: $color-primary-light;
						}
					}
						.sub-menu { // ul 2
							display: none;
							position: absolute;
							border: $color-gray-200 1px solid;
							box-shadow: 1px 1px 0 1px rgba(0,0,0,.2);
							li {
								width: 12rem;
							}
								a {
									background-color: white;
									border-bottom: $color-gray-200 1px dotted;
									&:hover {
										background-color: $color-gray-100;
									}
									&:before {
										margin-right: .5rem;
										content: "\f0da";
										color: $color-primary-light;
									}
								}
						}

						.menu-item-has-children:hover .sub-menu {
							display: block;
							animation: show-sub-menu .4s linear 0s;
							@keyframes show-sub-menu {
								from { opacity: 0; }
								  to { opacity: 1; }
							}
						}
						.menu-item-has-children:hover .sub-menu {}
			}
		}
		/**
		 * 
		 */
		@media($md_lg) {
			#primary-menu {
				text-align: center;
				.sub-menu { // ul
					text-align: left;
				}
			}
		}

		/**
		 * 
		 */
		@media($lg_) {
			#primary-menu {
				margin-left: auto;
			}
				.menu-item { // li
					&::first-child { // flexbox 
					  margin-right: auto;
					}
				}
		}



/**
 * form
 */
#site-navigation__search {
	input {}
	button {}
}
