*,
*:before,
*:after {
	box-sizing: border-box;
}

html {
	line-height: 1.7;
	font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI", Meiryo, "Yu Gothic", sans-serif; //  YuGothic ;
	// font-weight: 500;//Windows Medium 
	// font-feature-settings: "palt";
	// letter-spacing: .1em;
	// font-variant-numeric: slashed-zero tabular-nums;
	color: $color_font;
	-webkit-font-smoothing: antialiased; // Safari(Mac,iOS)
	-moz-osx-font-smoothing: grayscale; // Firefox
}

body {
	margin: 0;
}


/**
 * Adjustment
 */

// 
div,section,aside,header,footer,main,article,nav,
hgroup,h1,h2,h3,h4,h5,h6,
p,blockquote,ul,ol,li,dl,dt,dd,
figure,figcaption,video,
table,tbody,thead,tfoot,
form,fieldset,address,pre {
	// overflow-y: hidden;
	padding-top: .02px;
	padding-bottom: .02px;
}

// 
p,h1,h2,h3,h4,h5,h6,
th,td,dt,dd,label {
	word-wrap: break-word;
	word-break: break-all;
}

figure {
	margin-right: 0;
	margin-left: 0;
}

// 
pre {
	overflow-x: auto;
	line-height:.7rem;
}

hr {
	border-width: 0 0 1px 0;
}

/**
 * Typography
 */

// Size Set (You can use these font size variables directry.)
.font-xx-large { font-size: $font-xx-large; }
.font-x-large  { font-size: $font-x-large;  }
.font-large    { font-size: $font-large;    }
.font-small    { font-size: $font-small;    }
.font-x-small  { font-size: $font-x-small;  }

.bold {
	font-weight: bold;
}

h1,h2,h3,h4,h5,h6 {
	line-height: 1.4;
	small {
		display: block;
		font-weight: normal;
		font-size: $font-small;
	}
	small.inline {
		display: inline;
	}
}

a {
	color: $color-link;
	&:hover {
		color: $color-link-hover;
	}
	&:visited {
		color: $color-link-visited;
	}
}

textarea {
	max-width: 100%;
}

small {
	display: inline-block;
	line-height: 1.5em;
}

.warning {
	color: $color-red;
}

.color-primary {
	color: $color-primary;
}

.color-secondary {
	color: $color-secondary;
}

// 
.separator {
	color: $color-gray-400;
}

//  ()
.screen-reader-text {} // -> _wp-essential.scss



/**
 * FontAwesome (font-awesome)
 *   @link http://weboook.blog22.fc2.com/blog-entry-399.html
 */
.fa-facebook, .fa-facebook-square  { color: #3B5998; }
.fa-instagram { color: #262626; }
.fa-twitter   { color: #1DA1F2; }
.fa-line      { color: #00C300; }
.fa-youtube   { color: #CD201F; }

[class*="fa-"]:hover {
	filter: brightness(120%);
}



/**
 *
 * Utility Classes
 *
 */

.inline-block {
	display: inline-block;
}

// Device
//     Usage: 1. add class to element directory
//            2. add `@include mobile-small-text;` in css block
@mixin mobile-only {
	@media($sm_) {
		display: none;
	}
}
	.mobile-only {
		@include mobile-only;
	}

@mixin mobile-small-text {
	@media($_sm) {
		font-size: smaller;
	}
}
	.mobile-small-text {
		@include mobile-small-text;
	}

@mixin mobile-full-width {
	@media($_sm) {
		width: 100%;
	}
}
	.mobile-full-width {
		@include mobile-full-width;
	}

@mixin mobile-no-break {
	@media($_sm) {
		display: inline-block;
	}
}
	.mobile-no-break {
		@include mobile-no-break;
	}

@mixin mobile-small-text {
	@media($_sm) {
		font-size: smaller;
	}
}
	.mobile-small-text {
		@include mobile-small-text;
	}
	// -> .img--mobile-small in _object.scss


@media($_sm) {
	.mobile-br { // apply to only wbr element `<wbr class="mobile-br">`
		display: block;
	}

	// align: same settings as _wp-essential.scss
	.mobile-aligncenter {
		margin-left: auto;
		margin-right: auto;
		text-align: center;
	}
		img.mobile-aligncenter {
			display: block;
		}
	.mobile-alignleft {
		margin-right: .75em;
		text-align: left;
	}
		img.mobile-alignleft {
			float: left;
		}
}



// margin adjustment
.mt-0 { margin-top:      0 !important; }
.mt-1 { margin-top: .25rem !important; }
.mt-2 { margin-top:  .5rem !important; }
.mt-3 { margin-top:   1rem !important; }
.mt-4 { margin-top: 1.5rem !important; }
.mt-5 { margin-top:   2rem !important; }
.mt-6 { margin-top:   3rem !important; }

.mb-0 { margin-bottom:      0 !important; }
.mb-1 { margin-bottom: .25rem !important; }
.mb-2 { margin-bottom:  .5rem !important; }
.mb-3 { margin-bottom:   1rem !important; }
.mb-4 { margin-bottom: 1.5rem !important; }
.mb-5 { margin-bottom:   2rem !important; }
.mb-6 { margin-bottom:   3rem !important; }
.mb-0+*,.mb-1+*,.mb-2+*,.mb-3+*,
.mb-4+*,.mb-5+*,.mb-6+*,.mb-7+* {
	margin-top: 0 !important;
}

.my-0 {
	margin-top: 0 !important;
	margin-bottom: 0 !important;
}
.my-1 {
	margin-top: .25rem !important;
	margin-bottom: .25rem !important;
}
.my-2 {
	margin-top: .5rem !important;
	margin-bottom: .5rem !important;
}
.my-3 {
	margin-top: 1rem !important;
	margin-bottom: 1rem !important;
}
.my-4 {
	margin-top: 1.5rem !important;
	margin-bottom: 1.5rem !important;
}
.my-5 {
	margin-top: 2rem !important;
	margin-bottom: 2rem !important;
}
.my-6 {
	margin-top: 3rem !important;
	margin-bottom: 3rem !important;
}


// 
.border-bottom {
	border-bottom: 1px solid rgba(0,0,0,.1);
}


//  (100%)
.full-width {
	width: 100%;
}


// 
.box,
.box--white {
	margin-top: 1rem;
	margin-bottom: 1rem;
	padding: .5rem;
	>*:first-child {
		margin-top: 0;
	}
	>*:last-child {
		margin-bottom: 0;
	}
	@media($sm_){
		padding: 1rem;
	}
}
.box {
	border: 1px solid $color-gray-400;
}
.box--white {
	background-color: #FFF;
}



// 
.rounded-images img,
.rounded-images--5 img,
.rounded,
.rounded--5 {
	border-radius: 5px;
}
.rounded-images--3 img,
.rounded--3 {
	border-radius: 3px;
}
.rounded-images--7 img,
.rounded--7 {
	border-radius: 7px;
}
.rounded-images--10 img,
.rounded--10 {
	border-radius: 10px;
}

// 
.circle--images img,
.circle {
	border-radius: 9999px;
}

// Box Shadow
.box-shadow-images img,
.box-shadow {
	box-shadow: 0 1px 3px rgba(0,0,0,.3);
}

// Blur
.blur {
	filter: blur(7px);
}
